<template>
  <div>
    <input :value="modelValue" @input="handleInput" class="input" type="text" />
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
export default {
  props: ["modelValue"],
  data() {
    return {
      errorMessage: "",
    };
  },
  methods: {
    handleInput(event) {
      const inputValue = event.target.value;
      if (/^[0-9,.]*$/.test(inputValue)) {
        if (inputValue.length === 1 && [",", "."].includes(inputValue)) {
          event.target.value = "";
          this.errorMessage = "Type numbers here";
        } else {
          this.errorMessage = "";
          const sanitizedValue = +inputValue.replace(",", ".");
          this.$emit("update:model-value", sanitizedValue);
        }
      } else {
        this.errorMessage = "Type numbers here";
        event.target.value = "";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.input {
  padding: 0.5rem 1rem;
  border: none;
  background-color: #fff;
  border-radius: 1rem;
  filter: drop-shadow(2px 2px 4px #aaa);
  font-size: 1rem;

  &:focus-visible {
    outline: none;
  }
}

.error-message {
  color: red;
  font-size: 0.8rem;
  margin-top: 0.25rem;
}
</style>
